<template>
  <v-app id="app">
    <v-main>
      <v-container fluid class="fill-height d-flex flex-column justify-space-around">
        <play-palette @play="onPalettePlayBtnClick" @snapshot="onPaletteSnapshotBtnClick"/>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import PlayPalette from './components/PlayPalette'

export default {
  name: 'App',

  components: {
    PlayPalette
  },

  data: () => ({
    //
  }),

  methods: {
    onPalettePlayBtnClick (stream) {
      window.opener?.application?.oJVccBar?.SwitchVideoSource({
        mic: 1,
        mediaStream: stream,
        source: window.opener.LocalVideoSrc.Stream
      })
      console.info(stream)
    },

    onPaletteSnapshotBtnClick (callback) {
      callback(window.opener?.application?.oJVccBar?.GetRemoteSnapShot())
    }
  }
};
</script>

<style>
html, body, p {
  margin: 0;
  padding: 0;
}

#app {
  background-color: #AAAAAA;
}
</style>
