<template>
  <v-card elevation="20">
      <v-card-title>
        视频共享
        <v-spacer></v-spacer>
        <v-btn color="green" style="color: #FFF; margin-right: 11px;" @click="onChangeBackgroundBtnClick">
          加载本地图片
        </v-btn>

        <v-btn color="purple" style="color: #FFF; margin-right: 11px;" @click="onSnapBtnClick">
          截取视频图片
        </v-btn>

        <v-dialog
            v-model="dialog"
            width="240"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                color="red lighten-2"
                dark
                v-bind="attrs"
                v-on="on"
            >
              比例调整
            </v-btn>
          </template>

          <v-card>
            <v-card-title>画板分辨率调整</v-card-title>
            <v-card-text>
              <v-select
                  :items="['1024x768', '800x600', '640x480']"
                  label="画板大小"
                  v-model="canvasRatio"
              ></v-select>
            </v-card-text>
          </v-card>
        </v-dialog>

      </v-card-title>
      <v-card-text style="padding: 0 10px;">
        <div @mousemove="beginPath($event)">
          <canvas
              ref="palette"
              :width="canvasWidth"
              :height="canvasHeight"
              style="border: 1px solid #AAA"
              @mousedown="canvasDown($event)"
              @mouseup="canvasUp($event)"
              @mousemove="canvasMove($event)"
              @touchstart="canvasDown($event)"
              @touchend="canvasUp($event)"
              @touchmove="canvasMove($event)"
          >
          </canvas>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-icon :style="{color: color ? color.hexa : '#FFF'}">mdi-hexagon-slice-6</v-icon>
        <v-menu :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                text
                v-bind="attrs"
                v-on="on"
            >
              <span>颜色选取</span>
              <v-icon>mdi-eyedropper</v-icon>
            </v-btn>
          </template>
          <v-color-picker
              value="#F00FF"
              v-model="color"
              dot-size="20"
              hide-mode-switch
              mode="hexa"
              swatches-max-height="200"
          ></v-color-picker>
        </v-menu>
        <v-menu :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                text
                v-bind="attrs"
                v-on="on"
            >
              <span>线条粗细</span>
              <v-icon>mdi-format-line-weight</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item>
              <v-list-item-title>
                <v-text-field
                    v-model="drawWidth"
                    hide-details
                    single-line
                    type="number"
                ></v-text-field>
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
        <v-spacer></v-spacer>
        <v-btn @click="controlCanvas('prev')">
          上一步
        </v-btn>
        <v-btn @click="controlCanvas('next')">
          下一步
        </v-btn>
        <v-btn @click="controlCanvas('clear')">
          清除
        </v-btn>

        <v-spacer></v-spacer>

        <v-btn color="primary"  @click="onPlayBtnClick">
          播放
        </v-btn>

        <!--<v-btn color="error" text @click="$emit('close')">
          关闭
        </v-btn>-->

      </v-card-actions>
    </v-card>
</template>

<script>
export default {
  name: 'PlayPalette',

  context: null,
  data () {
    return {
      dialog: false,
      color: {
        hexa: '#FF0000FF',
        rgba: {
          r: 255,
          g: 0,
          b: 0,
          a: 1
        }
      },
      drawWidth: 2,

      imgUrl: [],
      isDrawing: false,
      // 存储当前表面状态数组-上一步
      preDrawAry: [],
      // 存储当前表面状态数组-下一步
      nextDrawAry: [],
      // 中间数组
      middleAry: [],

      frameCount: 0,

      canvasWidth: 640,
      canvasHeight: 480,

      playWidth: 640,
      playHeight: 480,

      canvasRatio: '640x480'
    }
  },

  watch: {
    canvasRatio (cr) {
      const [w, h] = cr.split('x')
      this.canvasWidth = parseInt(w)
      this.canvasHeight = parseInt(h)

      this.initDraw()
      this.setCanvasStyle()
    }
  },

  methods: {
    removeImg (src) {
      this.imgUrl = this.imgUrl.filter(item => item !== src)
    },

    initDraw () {
      this.context.fillStyle = 'white'
      this.context.fillRect(0, 0, this.canvasWidth, this.canvasHeight)
      const preData = this.context.getImageData(0, 0, this.canvasWidth, this.canvasHeight)
      // 空绘图表面进栈
      this.middleAry.push(preData)
    },

    beginPath (e) {
      const canvas = this.$refs.palette
      if (e.target !== canvas) {
        console.log('beginPath')
        this.context.beginPath()
      }
    },

    canvasMove ({offsetX, offsetY}) {
      if (this.isDrawing) {
        this.context.lineTo(offsetX, offsetY)
        this.context.stroke()
      }
    },

    // mouseup
    canvasUp () {
      console.log('canvasUp')
      const preData = this.context.getImageData(0, 0, this.canvasWidth, this.canvasHeight)
      if (!this.nextDrawAry.length) {
        // 当前绘图表面进栈
        this.middleAry.push(preData)
      } else {
        this.middleAry = []
        this.middleAry = this.middleAry.concat(this.preDrawAry)
        this.middleAry.push(preData)
        this.nextDrawAry = []
      }
      this.isDrawing = false
    },

    // mousedown
    canvasDown ({offsetX, offsetY}) {
      console.log('canvasDown')
      this.isDrawing = true

      this.setCanvasStyle()
      // 清除子路径
      this.context.beginPath()
      this.context.moveTo(offsetX, offsetY)
      console.log('moveTo', offsetX, offsetY)
      // 当前绘图表面状态
      const preData = this.context.getImageData(0, 0, this.canvasWidth, this.canvasHeight)
      // 当前绘图表面进栈
      this.preDrawAry.push(preData)
    },

    // 操作
    controlCanvas (action) {
      switch (action) {
        case 'prev':
          if (this.preDrawAry.length) {
            const popData = this.preDrawAry.pop()
            const midData = this.middleAry[this.preDrawAry.length + 1]
            this.nextDrawAry.push(midData)
            this.context.putImageData(popData, 0, 0)
          }
          break
        case 'next':
          if (this.nextDrawAry.length) {
            const popData = this.nextDrawAry.pop()
            const midData = this.middleAry[this.middleAry.length - this.nextDrawAry.length - 2]
            this.preDrawAry.push(midData)
            this.context.putImageData(popData, 0, 0)
          }
          break
        case 'clear':
          this.context.clearRect(0, 0, this.context.canvas.width, this.context.canvas.height)
          this.context.fillStyle = 'white'
          this.context.fillRect(0, 0, this.canvasWidth, this.canvasHeight)
          this.preDrawAry = []
          this.nextDrawAry = []
          this.middleAry = [this.middleAry[0]]
          break
      }
    },

    // 生成图片
    getImage () {
      const canvas = document.querySelector('#canvas')
      const src = canvas.toDataURL('image/png')
      this.imgUrl.push(src)
      // window.open(`data:text/plain,${src}`)
      window.location.href = src

    },

    // 设置绘画配置
    setCanvasStyle () {
      this.context.lineWidth = this.drawWidth
      this.context.shadowBlur = false
      this.context.shadowColor = this.color.hexa
      this.context.strokeStyle = this.color.hexa
    },

    onPlayBtnClick () {
      const stream = this.$refs.palette.captureStream(25)
      this.$emit('play', stream)
    },

    onSnapBtnClick () {
      this.$emit('snapshot', this.snapshotCallback)
    },

    snapshotCallback (base64) {
      console.info('Base64', base64)
      const img = new Image()
      img.onload = (arg) => {
        console.info(img.width, img.height, arg)
        this.context.drawImage(img, 0, 0, this.canvasWidth, this.canvasHeight)
      }
      img.src = base64
    },

    onChangeBackgroundBtnClick () {
      let elI = document.getElementById('_image_bg_input')
      if (!elI) {
        elI = document.createElement('input')
        elI.type = 'file'
        elI.accept = 'image/jpg, image/png,'
        elI.style.display = 'none'
        document.body.appendChild(elI)
        elI.onchange = (e) => {
          if (e.target.files && e.target.files[0]) {
            const f = new FileReader()
            f.onload = (e) => {
              const img = new Image()
              img.onload = (arg) => {
                console.info(img.width, img.height, arg)
                this.context.drawImage(img, 0, 0, this.canvasWidth, this.canvasHeight)
              }
              img.src = e.target.result
            }
            f.readAsDataURL(e.target.files[0])

          }
        }
      }
      elI.click()
    }
  },

  mounted () {
    const canvas = this.$refs.palette
    this.context = canvas.getContext('2d')
    this.initDraw()
    this.setCanvasStyle()
  }
}
</script>

